// image upload preview -- Andrei
#acidcodes_shortcodes_form {
  .media_image_holder {
    cursor: pointer;
    vertical-align: middle;
    width:200px;
    text-align: center;
    height: 140px;
    background: #ddd;
    border-radius:10px;
    padding-top: 20px;

    i {
      color: #eee;
      border-top: 16px;
      font-size: 100px;
      cursor: pointer;

      &.icon-edit {
        display:none;
        position: absolute;
        top: 35px;
        left: 70px;
        z-index:10;
      }
    }

    .upload_preview {
      display:none;
      position: absolute;
      top: 0px;
      left: 0px;
      z-index:9;
      border-radius: 10px;
      height: 100%;
      width: 100%;
    }

    &:hover i.active {
      display:block;
    }
  }
}

#TB_window {
  z-index: 1000500;
}

#acidcodes_shortcodes_form_modified {
  .param-tabs, .param-slider {

    //keep tabs vertical
    &.ui-tabs-vertical, &.ui-slider-vertical {
      width: 100%;
      .ui-tabs-nav, .ui-slider-nav {
        padding: 0;
        float: left;
        width: 16.66%;
        li { clear: left; width: 100%; border-bottom-width: 1px !important; border-right-width: 0 !important; margin: 0 -1px .2em 0; }
        li a { display:block; }
        li.ui-tabs-active {
          //padding-bottom: 0;
          padding-right: .1em;
          border-right-width: 1px;
        }
      }

      .ui-tabs-panel {
        padding: 1em 1em 1em 0;
        width: 83.33333%;
        float:left;
        // margin-left: 82px;
      }

      .tabs-heads, .slider-heads {
        li {
          width: 100%;
          padding: 5px;
          border-left: 1px solid #ccc;
          border-top: 1px solid #ccc;
          border-bottom: 1px solid #ccc;
          //@include border-top-left-radius(4px);
          //@include border-bottom-left-radius(4px);
          border-top-left-radius: 4px;
          border-bottom-left-radius: 4px;

          &.ui-state-active, &:hover {
            background-color: #f6f5f1;
          }

          a:focus, a:active {
            outline: none !important;
          }
        }
      }

      .tab, .slide {
        border: 1px solid #ccc;
        min-height: 300px;
        min-width: 450px;
        max-width: 83%;
        //@include border-top-right-radius(4px);
        //@include border-bottom-left-radius(4px);
        //@include border-bottom-right-radius(4px);
        border-top-right-radius: 4px;
        border-bottom-left-radius: 4px;
        border-bottom-right-radius: 4px;

        & > input, & > textarea {
          //@include border-radius(4px);
          border-radius: 4px;
          padding: 2%;
          margin: 2% 5%;
          width: 92%;
        }
        & a.tip_icon {
          position: relative;
          right: -7px;
          top: -33px;
          float: right;
        }
      }

    }
  }
}